import React from "react"
import "./about.css"
import Layout from "../components/layout"
import SEO from "../components/seo"

const AboutPage = () => (
  <Layout>
    <SEO title="About" />
    <div
          style={{
            margin: `5vw auto`,
            maxWidth: "70vw",
            padding: `0px 1.0875rem 1.45rem`,
            paddingTop: 0
          }}
    >
      <h2 style={{margin: "1rem auto", textAlign:"center"}}>ABOUT US</h2>
      <div className="about">
        <p style={{lineHeight: "1.8"}}>It all began 20 years ago when a good friend of mine asked me to go duck hunting with him. He loaned me a pair of waders and a “duck gun”, you know, the one that shoots those 3-1/2” shells. Well, as daybreak neared a small group of what sounded like airplanes swooshed overhead, cut back towards us and cuffed into the decoys. You probably know the rest of the story. It involves a lot more early mornings, a lot of time trying to find somewhere to hunt and a lot of strategy into decoy placement for the perfect set up.

          Republic Waterfowl was founded in 2017 by a family team committed to assisting outdoorsmen in finding places designated for public waterfowl hunting. We are the only online resource for helping you find destinations for waterfowl hunting near you.

          We are a small family team who understands that life-long friendships and memories can be made through one great experience. But it’s not just about the hunting, we are committed to conservation, protection of wildlife, and helping you connect with nature. There are so many different people from various cultural backgrounds involved in hunting. This is why we think it’s important to pass on our passion for waterfowl hunting that brings communities and people together.

          Our hope at Republic Waterfowl is to help you find a great place to hunt. We also hope you are able to make lifelong memories with people you care about. Thanks for stopping by and on behalf of my family and me, we wish you a lifetime of great outdoor memories!
        </p>
        <br/>
        <p>Sincerely,</p>
        <p>Cole Patton</p>
        <p>Founder Republic Waterfowl</p>
      </div>

    </div>
  </Layout>
)

export default AboutPage